.links {
    display: flex;
    align-items: center;
    margin-bottom: 30px;

}

.link {
    background: #23B473;
    box-shadow: inset 0px 0px 19px #FFFFFF;
    color: #fff;
    transition: all .3s;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 20px;
    font-size: 25px;
    font-weight: 700;
    line-height: 32px;


    &:hover {
        box-shadow: inset 0px 0px 19px 6px #FFFFFF;
    }
}

.link-circle {
    width: 200px;
    height: 200px;

}

.link-ellipse {
    width: 200px;
    height: 150px;
    text-transform: uppercase;
}

@media only screen and (max-width : 576px) {
    .links {
        flex-wrap: wrap;
        justify-content: center;
    }

    .link {
        margin-bottom: 20px;
        font-size: 18px;
        line-height: 24px;
    }

    .link-circle {
        width: 150px;
        height: 150px;

    }

    .link-ellipse {
        width: 150px;
        height: 112px;
    }

}
