.message {
    background: #393B3F;
    border-radius: 5px;
    text-align: center;
    color: #fff;
    position: relative;
    padding: 50px 70px;
    margin: 20px 0;
    width: 100%;
    max-width: 480px;

    &__title {
        margin-bottom: 40px;
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: 31px;
        letter-spacing: 0em;
        text-align: center;

    }

    &__subtitle {
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: 0em;
        margin-bottom: 25px;

        span {
            color: $green;
        }

    }

    &__icon {
        display: block;
        text-align: center;
    }

    .sentense {
        margin-bottom: 15px;
    }

}

.ic_close {
    width: 16px;
    height: 16px;
}

.message__close {
    position: absolute;
    right: 28px;
    top: 28px;
    cursor: pointer;

    &:hover {
        opacity: .7;
    }
}

@media only screen and (max-width : 576px) {
    .message {
        padding: 30px 35px 40px 35px;
        margin: 20px 0;

        &__title {
            margin-bottom: 20px;
            font-size: 20px;
            line-height: 26px;

        }

        &__subtitle {
            font-size: 18px;
            line-height: 23px;
            margin-bottom: 0;
        }

        &__icon {
            display: block;
            margin-top: 30px;

            img {
                width: 50px;
                height: 40px;
            }
        }
    }

    .message__close {
        right: 18px;
        top: 23px;
    }
}
