.frame-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.frame {
    padding-top: 30px;
    background: #393B3F;
    border-radius: 5px;
    color: #fff;
    width: 100%;
    max-width: 440px;
    margin-bottom: 30px;

    &__title {
        font-size: 30px;
        font-weight: 700;
        line-height: 39px;
        text-align: center;
        margin-bottom: 30px;
        text-transform: uppercase;

    }

    &__row {
        border-bottom: 1px solid rgba(179, 179, 179, 0.25);
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
    }

    .left-part {
        padding-left: 60px;
        padding-bottom: 10px;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: 0em;
        text-align: left;

    }

    .right-part {
        padding-right: 60px;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 26px;
        letter-spacing: 0em;
        text-align: right;

    }

    .frame-balance {
        margin-top: 48px;
        margin-bottom: 50px;
    }

}

.btn_wrp {
    display: flex;
    justify-content: center;
}

.btn-frame {
    padding: 17px 84px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.1em;
    text-align: center;
    color: #fff;
    border: none;
    border-radius: 5px 5px 0 0;
    background: #23B473;
    text-transform: uppercase;
    transition: all .3s;
    cursor: pointer;

    &:hover {
        box-shadow: inset 0px 0px 19px #FFFFFF;
    }

}

.frame-operator {
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0em;
    text-align: right;

}

.frame-operators {
    margin-bottom: 15px;
}

@media only screen and (max-width : 576px) {
    .frame {
        padding-top: 20px;

        &__row {
            margin-bottom: 10px;
        }

        &__title {
            font-size: 20px;
            margin-bottom: 10px;
            line-height: 26px;
        }

        .left-part {
            padding-left: 15px;
            font-size: 18px;
        }

        .right-part {
            padding-right: 15px;
            font-size: 18px;
        }

        .frame-operator {
            font-size: 18px;

        }

        .frame-balance {
            margin-top: 30px;
            margin-bottom: 30px;
        }

    }

    .btn-frame {
        padding: 8px 65px;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        letter-spacing: 0.07em;
        text-align: center;
        line-height: normal;

    }

    .frame-operators {
        margin-bottom: 5px;
    }
}
