.fblackpart {
    background: #202020;
    padding: 30px 61px 100px 61px;
    width: 100%;
    max-width: 560px;
    border-radius: 5px;
    position: relative;

    &__title {
        font-family: "Montserrat", sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: center;
        color: #fff;
        margin-bottom: 20px;
        text-transform: uppercase;
    }

}

.whitefields__input {
    width: 100%;
    height: 48px;
    background: #fff;
    box-shadow: none;
    border: none;
    border-radius: 5px;
    margin-bottom: 10px;
    display: block;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 600;
    line-height: 32px;



}



.btn-yellow {
    background: #F6E36A;
    border-radius: 5px;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: center;
    padding: 8px 0;
    width: 100%;
    border: none;
    margin-bottom: 15px;
    transition: all .3s;
    margin-bottom: 15px;
    position: relative;
    z-index: 90;
    cursor: pointer;

    &:hover {
        background: lighten(#F6E36A, 10%)
    }

}

.fblackpart-imgs {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 50;
}

.privacy-black {
    margin-bottom: 0;
}


@media only screen and (max-width : 576px) {

    .fblackpart {
        padding: 20px 15px 50px 15px;

        &__title {
            margin-bottom: 15px;
        }
    }

    .btn-yellow {
        margin-bottom: 20px;
    }

    .fblackpart-imgs {
        display: none;
    }

    .whitefields__input {
        padding: 0 10px;



    }

}
