.registration {
    display: flex;
    justify-content: center;
}

.freg {
    padding: 30px 40px 50px 40px;
    background: #393B3F;
    border-radius: 5px;
    max-width: 480px;

    &__title {
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: 31px;
        letter-spacing: 0em;
        text-align: center;
        color: #fff;
        margin-bottom: 38px;

    }

    &__input {
        border: none;
        background: transparent;
        color: #fff;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: 0em;
        text-align: left;


        &::placeholder {
            color: #B3B3B3;
        }

        height: 39px;
        width: 100%;
        border-bottom: 1px solid rgba(179, 179, 179, 0.25);
    }

    &__group {
        display: flex;
        margin-bottom: 20px;
    }

    &__btn {
        transition: all .3s;
        border: none;
        background: #fff;
        border-radius: 5px;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 23px;
        letter-spacing: 0em;
        text-align: center;
        width: 100%;
        max-width: 152px;
        margin-left: 30px;
        color: #393B3F;
        cursor: pointer;

        &:hover {
            background: $green;
            color: #fff;
        }

    }


}

.privacy {
    margin-bottom: 30px;
    position: relative;
    z-index: 90;
}

.fbtn-green {
    padding: 12px 58px;
    border: none;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0em;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
    background: $green;
    transition: all .3s;
    display: block;

    &:hover {
        box-shadow: inset 0px 0px 19px #FFFFFF;
    }

}

.btn-mes {
    padding: 12px 23px;
    width: fit-content;
    margin-top: 40px;
}

.btn-wrp {
    text-align: center;
    display: flex;
    justify-content: center;

}

@media only screen and (max-width : 576px) {
    .freg {
        padding: 20px 15px 40px 15px;

        &__group {
            flex-wrap: wrap;
        }

        &__title {
            margin-bottom: 30px;
            font-size: 20px;
            line-height: 26px;
        }

        &__input {
            font-size: 18px;
            line-height: 23px;
        }

        &__group {
            margin-bottom: 15px;
        }

        &__btn {
            width: 100%;
            transition: all .3s;
            border: none;
            background: #fff;
            border-radius: 5px;
            width: 100%;
            max-width: 100%;
            margin-left: 0px;
            padding: 6px 17px;
            margin-top: 15px;

        }
    }

    .privacy {
        margin-bottom: 20px;
    }
}
