.checkbox-hidden {
    display: none;
}

.privacy__check {

    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;

    &:not(:last-child) {
        margin-bottom: 15px;
    }

    .checkbox-wrap {

        display: flex;
        align-items: center;
        justify-content: center;
        width: 20px;
        max-width: 20px;
        flex: 0 0 20px;
        height: 20px;
        border-radius: 2px;
        border: 1px solid #B3B3B3;
        box-sizing: border-box;
        position: relative;
        margin-top: 5px;

        &::after {
            content: '';
            display: block;
            width: 25px;
            height: 18px;
            background: url("../img/check.svg") no-repeat 0 0;
            background-size: 100%;
            will-change: transform;
            transform: scale(0);
            opacity: 0;
            transition: all 0.1s linear;
            position: absolute;
            top: -5px;
            left: -1px;
        }
    }

    .checkbox-yel {
        &::after {
            content: '';
            display: block;
            width: 18px;
            height: 15px;
            background: url("../img/check-yel.svg") no-repeat 0 0;
            background-size: 100%;
            will-change: transform;
            transform: scale(0);
            opacity: 0;
            transition: all 0.1s linear;
            position: absolute;
            top: 0px;
            left: 3px;
        }

    }

    .checkbox-text {
        display: inline-block;
        margin-top: 1px;
        margin-left: 18px;
        font-size: 17px;
        font-style: normal;
        font-weight: 400;
        line-height: 22px;
        letter-spacing: 0em;
        text-align: left;
        color: #B3B3B3;

        a {
            color: #B3B3B3;
            text-decoration: underline;

        }

    }

    input {
        &:checked {
            + {
                .checkbox-wrap {
                    &::after {

                        transform: scale(1);
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.check-black {
    align-items: center;
}

@media only screen and (max-width : 576px) {
    .privacy__check {
        .checkbox-text {
            display: inline-block;
            margin-top: 1px;
            font-size: 14px;
            line-height: 18px;


        }


    }
}
