.frame__row {
    position: relative;
}

.select-frame {

    margin-right: -17px;
}

.select-frame__header {
    display: flex;
    align-items: center;
}

.select-frame__current {
    margin-right: 10px;

}

.select__body {
    position: absolute;
    top: 100%;
    width: 148px;
    height: 221px;
    background: #393B3F;
    border-radius: 5px;
    box-shadow: 0px 7px 12px rgba(0, 0, 0, 0.25);
    overflow-y: scroll;
    padding-top: 15px;
    padding-left: 20px;
    padding-bottom: 10px;
    scrollbar-width: thin;
    right: 20px;
    z-index: 90;
}

::-webkit-scrollbar {
    width: 5px;
    background: rgba(179, 179, 179, 0.15);


}

::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 20px #B3B3B3;
    border-radius: 5px;

}

::-moz-scrollbar-thumb {
    box-shadow: inset 0 0 8px #B3B3B3;
    border-radius: 5px;

}

.select-frame__item {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 5px;
    cursor: pointer;

    &:hover {
        opacity: .7;
    }

}

@media only screen and (max-width : 576px) {
    .select-frame {

        margin-right: 0px;
    }

    .select__body {
        position: absolute;
        top: 100%;
        width: 100%;
        height: auto;
        padding-top: 15px;
        padding-left: 15px;
        padding-bottom: 10px;
        scrollbar-width: thin;
        right: 0;
        left: 0;
        z-index: 90;
    }

    .select-frame__item {
        line-height: 23px;
        margin-bottom: 10px;

    }

}
